.searchbar {
    top: 0;
    left: 0;
    border-radius: 1px;
    position:sticky;
    z-index: 1100;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 64px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 12px;
    padding-bottom: 12px;
    color: #7c2c2c;
    background-color: #ff8181;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    }
    
    .searchForm {
      margin: 10px;
      display: flex;
      align-items: center;
      width: 100%;
      max-width: 600px;
      background-color: #ffc1c1;
      border: 1px solid #7c2c2c;
      border-radius: 3px;
    overflow: hidden;
    border-radius: 30px;
  }
  
  .searchForm-button {
    display: inline-block;
    width: 48px;
    height: 48px;
    border: 0;
    background-size: 10%;
    border-radius: 50px;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
  }
  
  input {
    all: unset;
    height: auto;
    color: #000000;
    background-color: #ffc1c1;
    margin-left: 10px;
  }
  .searchForm-button:hover {
    opacity: 1;
  }
  
  .searchForm-button-label {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(10%);
    border: 0;
    border-radius: 20px;

  }
  
  .searchForm-input {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
  }
  
  .searchForm-input::placeholder {
    font: inherit;
    font-size: 18px;
  }

  @media screen and (max-width: 550px) {

    .searchForm-button {
      display: inline-block;
      width: 58px;
      height: 48px;
      border: 0;
      background-size: 40%;
      border-radius: 30px;
      background-repeat: no-repeat;
      background-position: center;
      opacity: 0.6;
      transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
      cursor: pointer;
      outline: none;
    }
  }